    url("../../public/images/hands.jpg"); */
  background-image: linear-gradient(
      to right,
      rgba(28, 28, 29, 0.9),
      rgba(7, 52, 177, 0.2)
    ),
    url("../../../public/images/manandwoman.jpg");

  background-size: cover;

  background-position: top;

  background-attachment: fixed;
  height: calc(100vh);
  /* margin-top: 60px; */
  display: flex;
  flex-direction: column;
  /* align-items: center; */
